if (window.location.pathname === '/admin/recepcion') {
    // Seleccionar todos los enlaces con la clase "small-box-footer"
    const habitaciones = document.querySelectorAll('.small-box-footer');

    habitaciones.forEach(habitacion => {
        // Obtener los valores de los atributos data
        const id = habitacion.getAttribute('data-id');
        const estado = habitacion.getAttribute('data-estado');

        // Si la habitacin est en estado 3 o 6, mostrar alerta al hacer clic
        if (estado == 3 || estado == 6 || estado == 8) {
            habitacion.addEventListener('click', (event) => {
                event.preventDefault(); // Evita la redireccin por defecto
                
                Swal.fire({
                    title: "Limpieza Terminada?",
                    icon: "warning",
                    showCancelButton: true,
                    confirmButtonColor: "#3085d6",
                    cancelButtonColor: "#d33",
                    confirmButtonText: "S, confirmar!"
                }).then((result) => {
                    if (result.isConfirmed) {
                        let nuevoEstado; // Declarar variable correctamente
                        
                        if (estado == 3) {
                            nuevoEstado = 2;
                        } else if (estado == 6) {
                            nuevoEstado = 1;
                        } else if( estado == 8){
                            nuevoEstado = 5;
                        }

                        if (nuevoEstado !== undefined) {
                            // Llamar a la funcin para actualizar el estado
                            actualizarEstadoHabitacion(id, nuevoEstado);
                        } else {
                            console.error("Error: nuevoEstado no est definido.");
                        }
                    }
                });
            });
        }
    });

    // Funcin para actualizar el estado de la habitacin mediante PATCH
    function actualizarEstadoHabitacion(id, nuevoEstado) {
        fetch(`/api/habitaciones/${id}`, {
            method: "PATCH",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({
                id_estado_habitacion: nuevoEstado
            })
        })
        .then(response => {
            if (!response.ok) {
                throw new Error(`HTTP error! Status: ${response.status}`);
            }
            return response.json();
        })
        .then(data => {
            //console.log("Respuesta del servidor:", data);
            Swal.fire({
                title: "Habitacin nuevamente disponible",
                icon: "success"
            }).then(() => {
                location.reload(); // Recargar la pgina para reflejar cambios
            });
        })
        .catch(error => {
            console.error("Error:", error);
            Swal.fire({
                title: "Error al actualizar",
                text: "No se pudo cambiar la habitacin.",
                icon: "error"
            });
        });
    }
}